<script>
const base_url = "https://test.api.impjets.com/v1/";

export default {
  name: "Footer",

  data() {
    return {
      email: "",
      email_error: "",
      inProgress: false,
      mailSent: false,
    };
  },

  methods: {
    subscribe(e) {
      if (this.isCorrectEmail()) {
        // Send Mail
        this.inProgress = true;

        const formData = new FormData();
        formData.append("name", "not provided");
        formData.append("email", this.email);
        formData.append("msg", "User has subscribed to email.");

        fetch(base_url + "ext.charter/contact", {
          method: "POST",
          body: formData,
          cache: "no-cache",
        }).then(
          (response) => {
            this.inProgress = false;

            if (response.data.result !== undefined) {
              this.email = "";
              this.mailSent = true;
            }
          },
          (error) => {
            console.error(error);
          }
        );
      } else {
        this.email_error = "Please enter this field";
      }
    },
    validateEmail(email) {
      return email.match(
        /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      );
    },
    checkError(e) {
      if (this.isCorrectEmail()) {
        this.email_error = "";
      } else {
        this.email_error = "Please enter this field";
      }
    },
    isCorrectEmail() {
      if (this.email === "") {
        return false;
      } else {
        if (this.validateEmail(this.email)) {
          return true;
        } else {
          return false;
        }
      }
    },
  },
};
</script>
